import React, { useState } from 'react';
import { Check } from 'lucide-react';
import ButtonPill from './ButtonPill';

type CardType = 'cyan' | 'yellow' | 'purple' | 'pink';

interface BackgroundImages {
  default: string;
  hover?: string;
  clicked?: string;
}

interface PricingCardProps {
  package: any;
  type?: CardType;
  backgroundImages?: BackgroundImages;
  className?: string;
}

const DEFAULT_BACKGROUND_IMAGES: Record<CardType, BackgroundImages> = {
  cyan: {
    default: '/images/CardPricingBG/Cyan Normal Card BG.png',
    hover:   '/images/CardPricingBG/CyanHover Card BG.png',
    clicked: '/images/CardPricingBG/CyanClickedCardBG.png'
  },
  yellow: {
    default: '/images/CardPricingBG/Yellow Normal Card BG.png',
    hover:   '/images/CardPricingBG/Yellow Hover Card BG.png',
    clicked: '/images/CardPricingBG/Yellow Clicked Card BG.png'
  },
  purple: {
    default: '/images/CardPricingBG/Purple Normal Card BG.png',
    hover:   '/images/CardPricingBG/Purple Hover Card BG.png',
    clicked: '/images/CardPricingBG/Purple Clicked Card BG.png'
  },
  pink: {
    default: '/images/CardPricingBG/Pink Normal Card BG.png',
    hover:   '/images/CardPricingBG/Pink Hover Card BG.png',
    clicked: '/images/CardPricingBG/Pink Click Card BG.png'
  }
};

const PricingCard: React.FC<PricingCardProps> = ({
  package: pkg,
  type = 'cyan',
  backgroundImages,
  className = ''
}) => {
  const [clicked, setClicked] = useState(false);
  const bg = backgroundImages || DEFAULT_BACKGROUND_IMAGES[type];

  return (
    <div
      className={`
        group
        relative w-[180px] h-[340px] rounded-3xl overflow-hidden
        shadow-lg transition-all duration-300 ease-out
        ${className}
      `}
      onMouseDown={() => setClicked(true)}
      onMouseUp={() => setClicked(false)}
      onMouseLeave={() => setClicked(false)}
    >
      {/* DEFAULT BG */}
      <div
        className="absolute inset-0 bg-cover bg-center"
        style={{ backgroundImage: `url(${bg.default})` }}
      />

      {/* HOVER BG */}
      {bg.hover && (
        <div
          className="
            absolute inset-0 bg-cover bg-center opacity-0
            transition-opacity duration-300
            group-hover:opacity-100
          "
          style={{ backgroundImage: `url(${bg.hover})` }}
        />
      )}

      {/* CLICKED BG */}
      {bg.clicked && (
        <div
          className={`
            absolute inset-0 bg-cover bg-center
            transition-opacity duration-150
            ${clicked ? "opacity-100" : "opacity-0"}
          `}
          style={{ backgroundImage: `url(${bg.clicked})` }}
        />
      )}

      {/* Content */}
      <div className="relative z-10 p-5">
        <h3 className="text-base font-bold text-gray-900">{pkg.name}</h3>
        <p className="text-xs text-gray-700">For personal</p>

        {/* ...etc your content ... */}
      </div>
    </div>
  );
};

export default PricingCard;
export type { CardType };